a {
  margin: 0 4px;
  text-decoration: none;
  font-size: large;
  padding: 5px 10px;
  border-radius: 20px;
}

a:hover {
  background-color: rgba(238, 147, 133, 0.959);
  color: gray;
}
body {
  display: flex;
  justify-content: center;
}

#root {
  width: 80%;
}

.active {
  background-color: rgba(255, 0, 0, 0.788);
  color: white;
}

header {
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 1;
}

h1 {
  margin: 10px;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid gray;
}

.search input {
  width: 50px;
  transition: all 0.3s;
  padding: 5px 8px;
  border-radius: 20px;
  outline: none;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.search input:focus {
  outline: none;
  width: 100px;
}

input::placeholder,
textarea::placeholder {
  color: rgba(255, 0, 0, 0.329);
}
input:focus,
textarea:focus {
  background-color: rgba(211, 224, 223, 0.685);
}

.search :hover,
.search :valid {
  width: 100px;
}

header h1 {
  font-weight: 40px;
  font-style: italic;
}

h1 span,
h2 span {
  color: rgba(255, 0, 0, 0.788);
}

#contactPage {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#contactPage form {
  display: flex;
  flex-direction: column;
  width: 30%;
}

#contactPage h1 {
  text-align: center;
  color: rgba(255, 0, 0, 0.788);
}

#contactPage form div {
  width: 100%;
}

#contactPage form div input {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 20px;
  margin: 0;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

#contactPage form div input:focus {
  border: none;
  outline: none;
}

div {
  position: relative;
  margin: 10px 0;
}

div label {
  position: absolute;
  top: 50%;
  left: 6%;
  color: rgba(255, 0, 0, 0.329);
  transform: translateY(-50%);
  pointer-events: none;
  transition: all 0.2s ease;
}

input:focus + label,
input:valid + label {
  top: 0%;
  font-size: small;
  color: rgba(255, 0, 0, 0.788);
  background-color: white;
}

#contactPage form button {
  padding: 10px 20px;
  background-color: rgba(255, 0, 0, 0.788);
  border: none;
  color: white;
  margin-top: 10px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

#contactPage form textarea {
  margin: 10px 0;
  height: 20vh;
  border: none;
  padding: 10px 20px;

  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

#contactPage form textarea:focus {
  outline: none;
}

footer {
  height: 30vh;
}

#loginPage {
  display: flex;
  align-items: center;
  justify-content: center;
}

#loginPage h1 {
  text-align: center;
  color: rgba(255, 0, 0, 0.788);
  font-weight: 40px;
}

#loginPage section #createAccount {
  text-align: center;
}

#loginPage section {
  width: 30%;
  padding: 10px;
}

#loginPage section input {
  box-sizing: border-box;
  width: 100%;
  display: block;
  padding: 10px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

#loginPage section input:focus {
  border: none;
  outline: none;
}

#loginPage section button {
  width: 100%;
  padding: 10px 20px;
  background-color: rgba(255, 0, 0, 0.788);
  border: none;
  color: white;
  margin-top: 10px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.footer {
  width: 100%;
  background-color: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
  margin-top: 10px;
}

.footer-container {
  display: flex;
  justify-content: space-around;
}

.social-icon {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

.contact-info {
  margin-top: 10px;
}

.copyright {
  margin-top: 20px;
}

#loginPage section #forgetPassword {
  text-align: end;
}

#About section {
  display: flex;
  justify-content: space-between;
}

#About section div {
  width: 40%;
  line-height: 140%;
}

#About section .figure {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
}

#About section .figure img {
  width: 80%;
}
